* {
	background-color: #fff;
	font-family: sans-serif;
	background: none;
	font-family: 'Open Sans', sans-serif;
}

.df {
	display: -webkit-flex;
	display: -moz-flex;
	display: -ms-flex;
	display: -o-flex;
	display: flex;
}

.center {
	justify-content: center;
	-ms-align-items: center;
	align-items: center;
}

.column {
	-webkit-flex-direction: column;
	-moz-flex-direction: column;
	-ms-flex-direction: column;
	-o-flex-direction: column;
	flex-direction: column;
}

.red {
	border: 2px solid red;
}




.main {
	width: 100%;
	min-height: 100vh;
}

a {
	text-decoration: none;
}

a:hover {
	cursor: pointer;
}

.wrap {
	-webkit-flex-wrap: wrap;
	-moz-flex-wrap: wrap;
	-ms-flex-wrap: wrap;
	-o-flex-wrap: wrap;
	flex-wrap: wrap;
}





/* style.css */





.bloc {
	width: 100%;
}

.conteiner {
	max-width: 92.79%;
	width: 1250px;
	margin: 0 auto;
}

.bloc_1 {
	width: 100%;
	max-height: 100%;
	min-height: 659px;
	background-color: #f6f2f2;
}

.header {
	width: 100%;
	min-height: 90px;
  	background: #000;
}

.header a{
	height: 90px;
	padding: 0 30px;
	margin: 0;
	color: #fff;
	font-size: 14px;
  	font-family: "Open Sans";
  	font-weight: bold;
  	text-transform: uppercase;
  	line-height: 1.714;
  }

.header a:hover {
	background: #ff3f40;
}

.conteiner_bl1_bl2 {
	width: 100%;
}

.bloc_2_podbloc_1 {
	max-width: 100%;
	max-height: 100%;
	min-height: 570px;
	flex: 1 0 500px;
}

.podbloc {
	width: 49%;
	flex: 1 0 300px;
}

.podbloc_h1,
.podbloc_h2 {
	max-width: 100%;
	margin: 110px 15px 40px 15px;
  	font-size: 60px;
  	font-family: "Open Sans";
  	color: rgba(0, 0, 0, 0.8);
  	font-weight: bold;
  	text-transform: uppercase;
}

.podbloc_p_bl_1 {
	max-width: 100%;
	width: 500px;
	min-height: 100px;
	margin: 0 15px 30px 15px;
}

.podbloc_p_bl_2 {
	max-width: 100%;
	min-height: 100px;
	width: 500px;
	margin: 0 15px 30px 15px;
}

.bl_1_podbloc_1,
.bl_1_podbloc_2 {
	width: 49%;
}


.button_bl_1_pbl_1 {
	width: 225px;
	height: 60px;
  	border-radius: 5px;
  	border: 5px solid #ff2d2d;
  	margin: 15px;
}

.button_bl_1_pbl_1:hover {
	background-color: #ff2d2d;
}

.bloc_video {
	max-width: 100%;
	width: 550px;
	height: 350px;
  	background-color: #fff;
	color: #000;
	flex: 1 0 400px;
	margin: 30px; 
    box-shadow: 0px 14px 73px 0px rgba(0, 0, 0, 0.29);
}





.bloc_2 {
	margin-top: 110px;
	min-height: 100vh;
}

.bl_2_podbloc_h2 {
	margin-top: 0;
	padding-top: 0;
  	font-size: 42px;
  	font-family: "Open Sans";
  	color: rgba(0, 0, 0, 0.8);
  	font-weight: bold;
  	text-transform: uppercase;
}

.bl_2_ppbl {
	max-width: 100%;
	width: 290px;
	min-height: 230px;
	flex: 1 0 250px;
	margin: 0 30px 30px 0;
}

.bl_2_ppbl img {
	width: 45px;
	height: auto;
}







.bloc_3 {

}

.bl_3_pb_1 {
  font-size: 40px;
  font-family: "Open Sans";
  color: rgba(0, 0, 0, 0.8);
  font-weight: bold;
  text-transform: uppercase;
  line-height: 1.2;
}

.bl_3_pb_1 h2 {
	margin: 110px auto 30px auto;
	padding: 0;
}

.bl_3_pb_2 {
	max-width: 100%;
	width: 500px;
	min-height: 70px;
	margin: 0 15px;
}
.bl_3_pb_2 p {

}

.bl_3_bg_red {
	width: 132px;
	height: 3px;
	background-color: red;
}

.bl_3_p {
	margin: 0;
	padding: 0;
	-ms-align-items: center;
	align-items: center;
}

.bl_3_pb_3_red_polosa {
  background-color: rgb(255, 45, 45);
  width: 132px;
  height: 3px;
  margin: 0 auto;
}

.bl_3_pb_4_menu {
  font-size: 22px;
  font-family: "OpenSans";
  color: rgba(0, 0, 0, 0.702);
  text-transform: uppercase;
  margin: 90px auto 55px auto;
}

.bl_3_pb_4_menu a {
	margin: 0 auto;
}

.bl_3_pb_5 {
	width: 100%;
	margin-bottom: 70px;
}

.bl_3_pb_5 img {
	max-width: 100%;
	width: 290px;
	min-height: 290px;
	background-color: #f5f0dd;
	margin: 10px;
}


@media screen and (max-width: 950px) {

	.bloc_1_wrap-rewrs {
		-webkit-flex-wrap: wrap-reverse;
		-moz-flex-wrap: wrap-reverse;
		-ms-flex-wrap: wrap-reverse;
		-o-flex-wrap: wrap-reverse;
		flex-wrap: wrap-reverse;
	}	

	.podbloc_h2,
	.podbloc_h1 {
	  	font-size: 3em;
	  	font-family: "Open Sans";
	  	color: rgba(0, 0, 0, 0.8);
	  	font-weight: bold;
	  	text-transform: uppercase;
	}

	.bl_1_podbloc_1,
	.bl_1_podbloc_2 {
		width: 100%;
	}

	.bloc_video {
		max-width: 100%;
		height: 350px;
	  	background-color: #fff;
		margin: 10px; 
	    box-shadow: 0px 14px 73px 0px rgba(0, 0, 0, 0.29);
	}

	.bl_1_pbl_2 {
		margin: 0;
	}

	.bl_2_ppbl {
		justify-content: center;
		-ms-align-items: center;
		align-items: center;
	}

	.podbloc {
		width: 100%;
		-webkit-flex-direction: column;
		-moz-flex-direction: column;
		-ms-flex-direction: column;
		-o-flex-direction: column;
		flex-direction: column;
	}


}

@media screen and (max-width: 600px) {

	.header {
		-webkit-flex-direction: column;
		-moz-flex-direction: column;
		-ms-flex-direction: column;
		-o-flex-direction: column;
		flex-direction: column;
	}

	.header a:hover {
		background: #ff3f40;
	}
	
	.podbloc_h2,
	.podbloc_h1 {
		font-size: 1.6em;
	  	font-family: "Open Sans";
	  	color: rgba(0, 0, 0, 0.8);
	  	font-weight: bold;
	  	text-transform: uppercase;
	}
	
	.bl_1_podbloc_2 {
		max-width: auto;
		height: 300px;
	}

	.bloc_video {
		max-width: 100%;
		height: 300px;
	  	background-color: #fff;
	  	margin: 10px 0; 
	    box-shadow: 0px 14px 73px 0px rgba(0, 0, 0, 0.29);
	}

	

	.bloc_2_podbloc_2 {
		justify-content: center;
		-ms-align-items: center;
		align-items: center;
	}

	.bl_2_ppbl {
		justify-content: center;
		-ms-align-items: center;
		align-items: center;
	}

	.bl_3_pb_1_h2 {
		font-size: 1.6em;
	  	font-family: "Open Sans";
	  	color: rgba(0, 0, 0, 0.8);
	  	font-weight: bold;
	  	text-transform: uppercase;
	}

	.bl_3_pb_4_menu {
		-webkit-flex-direction: column;
		-moz-flex-direction: column;
		-ms-flex-direction: column;
		-o-flex-direction: column;
		flex-direction: column;
	}


}




